import { Component } from "react";
import http from "../http-common";

class ContactDataService {

    getAll(user) {
        return http.get(`/contacts`, {
            params: {
                unome: user.nome,
                ucognome: user.cognome,
            }
        });
    }

    /*     getAll(){
            return http.get(`/contacts`, {
                params: {
                    unome: this.state.user.nome,
                    ucognome: this.state.user.cognome,
                }
            });
        } */

    get(id) {
        return http.get(`/contacts/${id}`);
    }

    create(data) {
        return http.post("/contacts", data);
    }

    update(id, data) {
        return http.put(`/contacts/${id}`, data);
    }

    delete(id) {
        return http.delete(`/contacts/${id}`);
    }

    deleteAll() {
        return http.delete(`/contacts`);
    }

    findByHead(user, head, value) {
        return http.get(`/contacts`, {
            params: {
                unome: user.nome,
                ucognome: user.cognome,
                head,
                value
            }
        });
    }

    sendContact(contact) {
        var xhr = new XMLHttpRequest();
        var url = "http://gmeservice.it/contratti/DufercoBusiness.html";
        xhr.open("POST", url, true);
        xhr.setRequestHeader("Content-Type", "application/json");
        xhr.setRequestHeader("Access-Control-Allow-Origin", url);
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && xhr.status === 200) {
                var json = JSON.parse(xhr.responseText);
                console.log(json.email + ", " + json.nome);
            }
        };
        var data = JSON.stringify(contact);
        xhr.send(data);
    }

}

export default new ContactDataService();